import React from 'react';
import { Box, Button, TextField, Typography } from '@mui/material';

const Tab4 = () => {
  return (
    <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', bgcolor: '#4A1E2A', p: 2 }}>

      <Box sx={{ p: 1 }}>
        <Typography
          sx={{
            color: "#fff",
            fontWeight: 400,
            fontSize: { xs: "24px", md: "24px" },
            lineHeight: "100%",
            letterSpacing: "0%",
          }}
        >
          WAX in System
        </Typography>
        <Typography
          sx={{
            marginTop: 1,
            color: "#EDEDED",
            fontWeight: 700,
            fontSize: { xs: "24px", md: "24px" },
            lineHeight: "100%",
            letterSpacing: "0%",
          }}
        >
          11.3491 WAX
        </Typography>
      </Box>
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
        {/* Amount to Send */}
        <Box>
          <Typography
            sx={{
              color: "#fff",
              fontWeight: 400,
              fontSize: { xs: "24px", md: "24px" },
              lineHeight: "100%",
              letterSpacing: "0%",
            }}
          >
            Amount to withdraw
          </Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            <TextField
              variant="outlined"
              InputLabelProps={{ style: { color: "#fff" } }}
              InputProps={{
                sx: {
                  marginTop: 1,
                  width: "277px",
                  height: { xs: "30px", md: "47px" },
                  bgcolor: "#831F3F",
                  border: "none",
                },
              }}
            />
            <Typography
              sx={{ color: "#fff", fontSize: "0.9rem", marginBottom: 1 }}
            >
              WAX
            </Typography>
          </Box>
        </Box>
        {/* Request Button */}
        <Box>
          <Button
            variant="contained"
            sx={{
              border: '1px solid white',
              backgroundColor: "#882140",
              "&:hover": { backgroundColor: "#b71c1c" },
              color: "#fff",
              fontWeight: "bold",
              textTransform: "none",
            }}
          >
            Withdraw
          </Button>
        </Box>
      </Box>

    </Box>
  )
}

export default Tab4;