// components/DataCard.tsx
import React from 'react';
import { Card, CardContent, Typography, Box } from '@mui/material';


interface EarningItem {
  date: string;
  price: number;
}


const earningItems: readonly EarningItem[] = [
  {
    date: "Total Free CPU",
    price: 6.12354
  },
  {
    date: "Total Free CPU",
    price: 6.12354
  }, {
    date: "Total Free CPU",
    price: 6.12354
  }, {
    date: "Total Free CPU",
    price: 6.12354
  },
] as const;

const PriceCard = () => {
  return (

    <Box sx={{
      flex: 1,
      borderRadius: { xs: '20px', md: '50px' }, backgroundColor: '#831F3F', color: 'white',
      m: 1, p: { xs: 2, md: 3 },
      display: 'flex', flexDirection: 'column', justifyContent: 'space-between'
    }}>

      {earningItems && (
        <Box flex={1} mt={2} display="flex" flexDirection="column" gap={2} >
          {earningItems.map((earning, index) => (
            <Box flex={1} key={index} display="flex" flexDirection='column' justifyContent="space-between" gap={2}>
              <Typography align='center' key={index} variant="body2" color="#EBEBEB" sx={{ fontWeight: 400, fontSize: { xs: 12, sm: 16, md: 36 }, lineHeight: '100%', letterSpacing: '0%' }}>
                {earning.date}
              </Typography>
              <Typography align='center' key={index} variant="body2" color="#EBEBEB" sx={{ fontWeight: 500, fontSize: { xs: 24, sm: 32, md: 64 }, lineHeight: '100%', letterSpacing: '0%' }}>
                {earning.price}
              </Typography>
            </Box>
          ))}
        </Box>
      )}

    </Box>
  );
};

export default PriceCard;