import React from 'react';
import { Box, Typography } from '@mui/material';



const Banner = () => {
  return (
    <Box sx={{ mt: { xs: 3, md: 10 }, flex: 1, backgroundImage: "url('images/text_back.png')", backgroundSize: '130% 100%', backgroundPosition: 'center', transform: 'scaleX(-1)', p: 3, borderRadius: '50px' }}>
      <Box sx={{ display: 'flex', width: '100%', height: '100%', transform: 'scaleX(-1)', }}>
        <Box sx={{ width: '50%', p: 3 }}>
          <Typography
            sx={{
              fontWeight: 600,
              fontSize: { sm: 20, md: 36 },
              lineHeight: 1,
              letterSpacing: 0
            }}
            variant="h6"
            gutterBottom
          >
            Allowing anyone to rent CPU or deposit wax and earn from renting to others.
          </Typography>
          <Typography
            sx={{

              fontWeight: 300,
              fontSize: { sm: 16, md: 24 },
              lineHeight: 1,
              letterSpacing: 0
            }}
            variant="body2"
            paragraph
          >
            The price is dynamically set between a 1% and 10% fee and it changes everytime someone deposits/withdraws/rents/returns wax.
          </Typography>
          <Typography
            sx={{
              fontWeight: 300,
              fontSize: { sm: 16, md: 24 },
              lineHeight: 1,
              letterSpacing: 0
            }}
            variant="body2"
            paragraph
          >
            Users are limited to how much they can rent in a single transaction but can make multiple requests as long as there is wax available.
            There is also a free system implemented but it has limits too.
          </Typography>
        </Box>
        <Box sx={{ width: '50%', position: 'relative' }}>
          <Box sx={{ overflow: 'hidden', position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', width: '110%', height: '150%' }}>
            <Box sx={{
              position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', width: '180%', height: '60%',
              display: 'flex', justifyContent: 'center', alignItems: 'center'
            }}>
              <Box sx={{ position: 'relative', width: '70%', m: 1 }}>
                <img src='images/side1.png' alt='side1' style={{ width: '100%', display: 'block' }} />
                <Box sx={{ position: 'absolute', top: 0, left: '70%', width: '15%', transform: 'translate(-50%, -50%)' }}>
                  <img src='images/circle_dynamic_roller.png' alt='side1'
                    style={{ width: '100%', display: 'block' }} />
                </Box>
                <Box sx={{ position: 'absolute', top: '95%', left: '65%', width: '15%', transform: 'translate(-50%, -50%)' }}>
                  <img src='images/circle_dynamic_roller.png' alt='side1'
                    style={{ width: '100%', display: 'block' }} />
                </Box>
                <Box sx={{ position: 'absolute', top: '40%', left: '83%', width: '12%', transform: 'translate(-50%, -50%)' }}>
                  <img src='images/circle_dynamic_roller.png' alt='side1'
                    style={{ width: '100%', display: 'block' }} />
                </Box>
                <Box sx={{ position: 'absolute', top: '76%', left: '50%', width: '8%', transform: 'translate(-50%, -50%)' }}>
                  <img src='images/circle_dynamic_roller.png' alt='side1'
                    style={{ width: '100%', display: 'block' }} />
                </Box>
                <Box sx={{ position: 'absolute', top: '34%', left: '41%', width: '5%', transform: 'translate(-50%, -50%)' }}>
                  <img src='images/circle_dynamic_roller.png' alt='side1'
                    style={{ width: '100%', display: 'block' }} />
                </Box>
                <Box sx={{ position: 'absolute', top: '68%', left: '69%', width: '4%', transform: 'translate(-50%, -50%)' }}>
                  <img src='images/circle_dynamic_roller.png' alt='side1'
                    style={{ width: '100%', display: 'block' }} />
                </Box>
                <Box sx={{ position: 'absolute', top: '46%', left: '56%', width: '2%', transform: 'translate(-50%, -50%)' }}>
                  <img src='images/circle_dynamic_roller.png' alt='side1'
                    style={{ width: '100%', display: 'block' }} />
                </Box>
              </Box>

            </Box>
          </Box>

        </Box>
      </Box>
    </Box>
  );
};

export default Banner;

