import React, { useState } from 'react';
import {
  TextField,
  Button,
  Container,
  Typography,
  Box,
} from '@mui/material';
import InputField from './InputField';



function Second({ setStage }: { setStage: (stage: number) => void }) {
  const [actionName, setActionName] = useState('');
  const [contractAddress, setContractAddress] = useState('');
  const [actions, setActions] = useState(['', '', '', '']);
  const [data, setData] = useState(['', '', '', '']);
  const [cpu, setCpu] = useState('');
  const [paymentMethod, setPaymentMethod] = useState('wax');



  const addActionField = () => {
    setActions([...actions, '']);
    setData([...data, '']);
  };

  const handleSubmit = () => {
    console.log('Submitted:', {
      actionName,
      contractAddress,
      actions,
      data,
      cpu,
      paymentMethod,
    });
    setStage(1);
    // Implement your submit logic here
  };

  const handleReset = () => {
    setActionName('');
    setContractAddress('');
    setActions(['', '', '', '']);
    setData(['', '', '', '']);
    setCpu('');
    setPaymentMethod('wax');
  };

  return (
    <Box sx={{ backgroundColor: '#4A1E2A', padding: '20px', color: 'white', pb: { xs: 4, md: 10 } }}>
      <Container maxWidth="md">
        <Box mt={2} display="flex" flexDirection={{ xs: 'column', md: 'row' }} justifyContent="flex-end" gap={2}>
          <Box sx={{ flex: 1 }}>
            <Typography sx={{
              fontWeight: 600,
              fontSize: { xs: "24px", md: "36px" },
              lineHeight: "100%",
              letterSpacing: "0%",
            }}>
              LimitlessWAX
            </Typography>
          </Box>
          <Button variant="contained" sx={{ textTransform: 'none', backgroundColor: '#831F3F', color: 'white', width: { xs: '100%', md: 'auto' } }}
            onClick={() => setStage(1)}>Save</Button>
          <Button variant="contained" sx={{ textTransform: 'none', backgroundColor: '#831F3F', color: 'white', width: { xs: '100%', md: 'auto' } }}
            onClick={() => setStage(1)}>Load</Button>
        </Box>
        <Box sx={{ mt: 2, width: '50%' }}>
          <InputField label="Name this action" />
        </Box>
        <Box sx={{ mt: 2, width: '50%' }}>
          <InputField label="Smart contract address" />
        </Box>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          {actions.map((action, index) => (
            <Box key={index} sx={{ display: 'flex', gap: 2 }}>
              <Box sx={{ flex: 1 }}>
                <InputField label={`Action #${index + 1}`} />
              </Box>
              <Box sx={{ flex: 1 }}>
                <InputField label={`Data #${index + 1}`} />
              </Box>
            </Box>
          ))}
        </Box>
        <Button variant="outlined" sx={{ color: 'white', border: '2px dotted white', textTransform: 'none' }} onClick={addActionField} >
          Add Action +
        </Button>
        <Box sx={{ display: 'flex', gap: 2, mt: 2 }}>
          <Box sx={{ flex: 1 }}>
            <Typography sx={{ fontWeight: 400, fontSize: { xs: '16px', md: '24px' }, lineHeight: '100%', letterSpacing: '0%' }}>
              How much CPU
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <TextField
                margin="normal"
                variant="outlined"
                InputProps={{
                  sx: {
                    p: 0,
                    height: "40px",
                    bgcolor: "#831F3F"
                  }
                }}
              />
              <Typography sx={{ fontWeight: 700, fontSize: { xs: '16px', md: '24px' }, lineHeight: '100%', letterSpacing: '0%' }}>
                ms
              </Typography>
            </Box>
          </Box>
          <Box sx={{ flex: 1 }}>
            <Typography sx={{ fontWeight: 400, fontSize: { xs: '16px', md: '24px' }, lineHeight: '100%', letterSpacing: '0%' }}>
              Pay with
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mt: 2 }}>
              <Button variant="contained" sx={{ backgroundColor: '#831F3F', color: 'white', height: 40 }}
                onClick={() => setPaymentMethod('wax')}>WAX</Button>

              <Typography sx={{ fontWeight: 400, fontSize: { xs: '16px', md: '24px' }, lineHeight: '100%', letterSpacing: '0%' }}>
                or
              </Typography>
              <Button variant="outlined" sx={{ color: 'white', height: 40, border: '2px dotted white' }}
                onClick={() => setPaymentMethod('custom')}>Custom Token +</Button>
            </Box>
          </Box>
        </Box>

        <Box mt={3}>
          <Button variant="contained" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={handleSubmit} style={{ marginRight: '10px' }}>
            Submit
          </Button>
          <Button variant="outlined" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={handleReset}>
            Reset
          </Button>
        </Box>


      </Container>
    </Box>
  );
}

export default Second;