import React from 'react';
import { Box, AppBar, Toolbar, Typography, Button, Container, Grid, Avatar, Link } from '@mui/material';
import { styled } from '@mui/material/styles';
import Stay from '../componnets/Stay';
import Banner from './Banner';
import Person from './Person';
// Define theme colors to match the image
const primaryColor = '#BE3144'; // Main Brand Color
const secondaryBackgroundColor = '#292323';
const footerBackgroundColor = '#1a1616';
const textColor = '#ffffff'; // Default Text Color


const AboutUs = () => {
  return (
    <Box sx={{ flexGrow: 1, color: textColor, fontFamily: 'Arial' }}>

      {/* Main Content */}
      <Container maxWidth="lg" sx={{ textAlign: 'center' }}>
        <Banner />

        {/* Stay up to date */}
        <Stay />

        {/* Team Section */}
        <Box sx={{ mt: 5, display: 'flex', justifyContent: 'space-around', flexWrap: 'wrap' }}>
          <Person name="Vaaaan" description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Augue etiam auctor massa etiam." />
          <Person name="deraXyna" description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Augue etiam auctor massa etiam." />

        </Box>

        {/* Partners Section */}
        <Typography variant="h5" component="h3" sx={{ mt: 6, mb: 3, fontWeight: 'bold' }}>
          Partners
        </Typography>
        <Box sx={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', gap: { xs: 2, md: 4 } }}>
          {Array.from({ length: 6 }).map((_, index) => (
            <Box
              key={index}
              position='relative'
              sx={{
                width: 96,
                height: 72,
                borderRadius: 1,
                opacity: 0.5,
              }}
            >
              <img src="images/blank.png" alt='partner'
                style={{
                  position: 'absolute', top: '50%', left: '50%',
                  transform: 'translate(-50%, -50%)', width: '200%', height: '300%', filter: 'brightness(0) invert(1)'
                }} />
            </Box>
          ))}
        </Box>
      </Container >


    </Box >
  );
};

export default AboutUs;