import React from 'react';
import { Box, Button, TextField, Typography } from '@mui/material';

const Tab2 = () => {
  return (
    <Box sx={{ flex: 1, display: 'flex', bgcolor: '#4A1E2A', p: 2 }}>
      <Box sx={{ flex: 1, p: 2, display: 'flex', flexDirection: 'column', gap: 2 }}>
        {/* Amount to Send */}
        <Box>
          <Typography
            sx={{ color: "#fff", fontWeight: 400, fontSize: { xs: "16px", md: "24px" }, marginBottom: 1 }}
          >
            Amount to send:
          </Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            <TextField
              InputProps={{
                sx: {
                  p: 0,
                  height: "40px",
                  bgcolor: "#831F3F"
                }
              }}

            />
            <Typography
              sx={{ color: "#fff", fontSize: "0.9rem", marginBottom: 1 }}
            >
              WAX
            </Typography>
          </Box>

        </Box>

        {/* Number of Days */}
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <Box>
            <Typography
              sx={{ color: "#fff", fontWeight: 400, fontSize: { xs: "16px", md: "24px" }, marginBottom: 1 }}
            >
              Number of days:
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <TextField
                InputProps={{
                  sx: {
                    p: 0,
                    width: '85px',
                    height: "40px",
                    bgcolor: "#831F3F"
                  }
                }}

              />
              <Typography
                sx={{ color: "#fff", fontSize: "0.9rem", marginBottom: 1 }}
              >
                Days
              </Typography>
            </Box>
          </Box>
          <Box>
            <Typography
              sx={{ color: "#fff", fontWeight: 400, fontSize: { xs: "16px", md: "24px" }, marginBottom: 1 }}
            >
              User to stake:
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <TextField
                InputProps={{
                  sx: {
                    p: 0,
                    width: '250px',
                    height: "40px",
                    bgcolor: "#831F3F"
                  }
                }}
              />
            </Box>
          </Box>


        </Box>

        {/* Amount to be Staked */}
        <Box>
          <Typography
            sx={{ color: "#fff", fontWeight: 400, fontSize: { xs: "16px", md: "24px" }, marginBottom: 1 }}
          >
            Amount to be staked:
          </Typography>
          <Typography
            sx={{
              color: "#fff",
              fontWeight: 700,
              fontSize: { xs: "16px", md: "24px" },
              lineHeight: "100%",
              letterSpacing: "0%",
              marginBottom: 2,
            }}
          >
            11.349123158 WAX
          </Typography>
        </Box>

        {/* Request Button */}
        <Box>
          <Button
            variant="contained"
            sx={{
              border: '1px solid white',
              backgroundColor: "#882140",
              "&:hover": { backgroundColor: "#b71c1c" },
              color: "#fff",
              fontWeight: "bold",
              textTransform: "none",
            }}
          >
            Stake to user
          </Button>
        </Box>
      </Box>

      {/* ROI Section */}

    </Box>
  )
}

export default Tab2;