import React from 'react';
import { AppBar, Toolbar, Typography, Button, Box, useMediaQuery, IconButton, Theme, Link } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link as RouterLink } from 'react-router-dom';

interface NavigationItem {
  label: string;
  path: string;
}

const navigationItems: NavigationItem[] = [
  { label: 'About Us', path: '/aboutus' },
  { label: 'Account', path: '/account' },
  { label: 'Cpu4sale', path: '/cpu4sale' },
  { label: 'Limitlesswax', path: '/limitlesswax' },
];

const Header: React.FC = () => {
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));

  const handleNavigation = (path: string): void => {
    window.location.href = path;
  };

  return (
    <Box position="static" sx={{
      boxShadow: 'none',
      pt: 3,
      bgcolor: 'transparent' // dark background color
    }}>
      <Toolbar style={{ justifyContent: "space-between", padding: 0 }} >
        <Link to="/" component={RouterLink} >
          <img src="images/limitless1.png" style={{ width: "44px " }} />
        </Link>
        {isMobile ? (
          <Box sx={{ display: 'flex', gap: 2 }}>
            <IconButton edge="start" color="inherit" aria-label="menu">
              <MenuIcon />
            </IconButton>
            <Button
              variant='outlined'
              key={'/login'}
              color="inherit"
              sx={{ color: "#FFFFFF", px: { xs: 1, md: 4 }, py: 1, textTransform: 'none', fontWeight: 600, fontSize: { xs: 12, md: 20 }, lineHeight: '115%' }}
              onClick={() => handleNavigation('/login')}
            >
              {'Login'}
            </Button>
          </Box>

        ) : (
          <Box sx={{ display: 'flex', alignItems: 'center', gap: { xs: 2, md: 5 } }}>
            {navigationItems.map((item: NavigationItem) => (
              <Link
                key={item.path}
                to={item.path}
                component={RouterLink}
                sx={{ color: "#E9E9E9", textDecoration: 'none', fontWeight: 400, fontSize: { xs: 12, md: 18 }, lineHeight: '100%' }}
              >
                {item.label}
              </Link>
            ))}
            <Button
              variant='outlined'
              key={'/login'}
              color="inherit"
              sx={{ color: "#FFFFFF", px: { xs: 1, md: 4 }, py: 1, textTransform: 'none', fontWeight: 600, fontSize: { xs: 12, md: 20 }, lineHeight: '115%' }}
              onClick={() => handleNavigation('/login')}
            >
              {'Login'}
            </Button>
          </Box>
        )}
      </Toolbar>
    </Box>
  );
};

export default Header;