import React from 'react';
import { Box, Button, TextField, Typography } from '@mui/material';

const Tab3 = () => {
  return (
    <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', bgcolor: '#4A1E2A', p: 2 }}>
      <Box sx={{ flex: 1, display: 'flex' }}>
        <Box sx={{ p: 1 }}>
          <Typography
            sx={{
              color: "#fff",
              fontWeight: 400,
              fontSize: { xs: "16px", md: "24px" },
              lineHeight: "100%",
              letterSpacing: "0%",
            }}
          >
            WAX Balance
          </Typography>
          <Typography
            sx={{
              marginTop: 1,
              color: "#fff",
              fontWeight: 700,
              fontSize: { xs: "16px", md: "24px" },
              lineHeight: "100%",
              letterSpacing: "0%",
            }}
          >
            11.3491 WAX
          </Typography>
        </Box>
        <Box sx={{ p: 1 }}>
          <Typography
            sx={{
              color: "#fff",
              fontWeight: 400,
              fontSize: { xs: "16px", md: "24px" },
              lineHeight: "100%",
              letterSpacing: "0%",
            }}
          >
            WAX in System
          </Typography>
          <Typography
            sx={{
              marginTop: 1,
              color: "#fff",
              fontWeight: 700,
              fontSize: { xs: "16px", md: "24px" },
              lineHeight: "100%",
              letterSpacing: "0%",
            }}
          >
            11.3491 WAX
          </Typography>
        </Box>
      </Box>
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
        {/* Amount to Send */}
        <Box>
          <Typography
            sx={{
              marginTop: 1,
              color: "#fff",
              fontWeight: 400,
              fontSize: { xs: "16px", md: "24px" },
              lineHeight: "100%",
              letterSpacing: "0%",
            }}
          >
            Amount to send:
          </Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            <TextField
              variant="outlined"
              InputLabelProps={{ style: { color: "#fff" } }}
              InputProps={{
                sx: {
                  marginTop: 1,
                  width: "277px",
                  height: "47px",
                  bgcolor: "#831F3F",
                  border: "none",
                },
              }}
            />
            <Typography
              sx={{
                color: "#fff",
                fontWeight: 700,
                fontSize: { xs: "24px", md: "24px" },
                lineHeight: "100%",
                letterSpacing: "0%",
              }}
            >
              WAX
            </Typography>
          </Box>

        </Box>





        {/* Request Button */}
        <Box>
          <Button
            variant="contained"
            sx={{
              border: '1px solid white',
              backgroundColor: "#882140",
              "&:hover": { backgroundColor: "#b71c1c" },
              color: "#fff",
              fontWeight: "bold",
              textTransform: "none",
            }}
          >
            Deposit and Earn
          </Button>
        </Box>
      </Box>

    </Box>
  )
}

export default Tab3;