import React from 'react';
import { Box, Typography, Button } from '@mui/material';

const Stay = () => {
  return (
    <Box sx={{
      mt: { xs: 5, md: 10 }, py: { xs: 2, md: 7 }, px: { xs: 2, md: 3 },
      display: 'flex', justifyContent: 'space-around', alignItems: 'center', borderRadius: '50px', bgcolor: "#331E22CC"
    }}>
      <Typography sx={{
        color: '#EFEFEF', fontWeight: 700, fontSize: { sm: 24, md: 36 }, lineHeight: 1, letterSpacing: 0, textAlign: 'center'
      }}>
        Stay up to date with Limitless WAX
      </Typography>
      <Button variant="contained"
        sx={{ textTransform: 'none', color: 'white', bgcolor: '#8C243F', border: '1px solid white' }}
      >
        Join Community
      </Button>
    </Box>
  );
};

export default Stay;
