// components/DataCard.tsx
import React from 'react';
import { Card, CardContent, Typography, Box } from '@mui/material';

interface DataCardProps {
  title: string;
  value: string;
  earnings?: string[];
  isStacked?: boolean;
}

const DataCard: React.FC<DataCardProps> = ({ title, value, earnings, isStacked }) => {
  return (
    <Box sx={{
      borderRadius: { xs: '20px', md: '50px' }, backgroundColor: '#4A1E2A', color: 'white',
      m: 1, p: 2,
      display: 'flex', flexDirection: 'column', justifyContent: 'space-around', alignItems: 'center'
    }}>
      <Typography sx={{
        color: '#EBEBEB', fontWeight: 400,
        fontSize: { xs: '16px', md: '32px' }, lineHeight: '100%', letterSpacing: '0%'
      }}
        variant="subtitle2" color="textSecondary" gutterBottom>
        {title}
      </Typography>
      <Typography sx={{
        color: '#EBEBEB', fontWeight: 500,
        fontSize: { xs: '24px', md: '64px' }, lineHeight: '100%', letterSpacing: '0%'
      }} variant="h6">{value}</Typography>

    </Box>
  );
};

export default DataCard;