import React from 'react';
import { Box, Typography } from '@mui/material';

interface PersonProps {
  name: string;
  description: string;
}

const Person: React.FC<PersonProps> = ({ name, description }) => {
  return (
    <Box sx={{ width: { xs: 110, md: 300 }, maxWidth: '500px', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      <Box sx={{ width: { xs: 100, md: 198 }, height: { xs: 100, md: 198 }, borderRadius: '50%', bgcolor: '#8C243F' }} />
      <Typography
        sx={{ fontWeight: 500, fontSize: { xs: 24, md: 48 }, lineHeight: 1, letterSpacing: 0 }}
        variant="h6" gutterBottom >
        {name}
      </Typography>
      <Typography
        sx={{
          fontWeight: 400, fontSize: { xs: 16, md: 24 }, lineHeight: 1, letterSpacing: 0, textAlign: 'center'
        }}
        variant="body2">
        {description}
      </Typography>
    </Box>
  );
};

export default Person;

