// components/ActionButton.tsx
import React from 'react';
import { Button, Typography } from '@mui/material';

interface ActionButtonProps {
  label: string;
  onClick?: () => void;
}

const ActionButton: React.FC<ActionButtonProps> = ({ label, onClick }) => {
  return (
    <Button
      fullWidth
      variant="contained"
      onClick={onClick}
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        color: '#EBEBEB',
        bgcolor: '#5C2736',
        borderRadius: { xs: '20px', md: '50px' },
        p: { xs: 2, md: 3 },
        boxShadow: '10px 10px 5px 0px #00000026',
      }}
    >
      <Typography sx={{
        fontWeight: 400,
        fontSize: { xs: 12, md: 24 },
        lineHeight: 1,
        letterSpacing: 0,
        textAlign: 'center',
        textTransform: 'none'
      }}>
        {label}
      </Typography>
      <Typography sx={{
        fontFamily: 'Roboto', fontWeight: 800, fontSize: { xs: 24, md: 64 }, lineHeight: '100%', letterSpacing: '0%'
      }}>
        &#62;
      </Typography>
    </Button>
  );
};

export default ActionButton;